:root {
    --primary-color: #1976d2;
    --light-primary-color: #64b5f6;
    --dark-primary-color: #0d47a1;
    --accent-color: #ffd600;
    --primary-text: #212121;
    --secondary-text: #757575;
    --third-text: #bfbfbf;
    --text: #ffffff;
    --divider-color: #f1f1f1;
    --shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
    --shadow-hover: 0 3px 6px -4px rgba(0, 0, 0, 0.16), 0 6px 16px 0 rgba(0, 0, 0, 0.12), 0 9px 28px 8px rgba(0, 0, 0, 0.09);
}

.App {
    background-color: var(--divider-color);
    min-height: 100vh;
    color: var(--primary-text);
}

.BackTop {
    background-color: var(--primary-color);
    color: var(--text);
    border-radius: 100px;
    font-size: 50px;
    padding: 10px;
}

@media (max-width: 480px) {
    .BackTop {
        position: relative;
        right: 30px;
    }
}